<template>
  <div class="images_body">

    <!-- 背景音乐播放 -->
    <div class="music_body">
      <img src="@/assets/img/pig.jpg" class="media_img" alt="" @click="clickPigImg()">
      <audio :src="musicSrc" class="media-audio" loop :autoplay="musicAutoPlay" ref="MusicPlay"></audio>
    </div>

    <!-- 加载星空 -->
    <stars-show></stars-show>

    <!-- 3D 图片展示 -->
    <carousel-3d
      :autoplay="false"
      :autoplayTimeout="3000"
      :perspective="35"
      :startIndex="0"
      :display="displayNumbers"
      :animationSpeed="1000"
      :clickable="slideClickable"
      :width="slideSize.width"
      :height="slideSize.height"
      :controlsVisible="controlsVisible"
      :controlsHeight="60"
      @onSlideChange="switchingSlide()"
    >
      <slide v-for="(item, i) in slides" :index="i" :key="i">
        <template slot-scope="obj">
          <img :src="item.src" class="show_img" @click="doubleClickSlide(obj)" />
        </template>
      </slide>
    </carousel-3d>

  </div>
</template>

<script>
import StarsUtil from './stars.vue'
import utils from "@/utils/common"
import cookie from "@/utils/cookies"
export default {
  name: "DisplayImages",
  components: {
    "stars-show": StarsUtil
  },
  data () {
    return {
      musicSrc: require("@/assets/media/love_you-wangxinling.mp3"),
      musicIconTurn: true,
      musicAutoPlay: false,
      slideSize: {
        width: 520,
        height: 520
      },
      slideClickable: true,
      controlsVisible: true,
      displayNumbers: 5,
      slides: [],
      clickCount: 0,
      size: {}
    }
  },
  watch: {
    slides: function (val) {
      this.slides = val
    },
    displayNumbers: function (val) {
      this.displayNumbers = val
    },
    slideSize: function (val) {
      this.slideSize = val
    }
  },
  created () {
    utils.clickCreateStars()
    
    if (!cookie.getCookie('login_key')) {

      // giuhub pages 页面不支持 router 跳转, 需要使用另一个输入通行码方式
      if (process.env.NODE_ENV === "production") {
        let inputPasscode = prompt("请输入通行码:")
        const { result, message } = utils.validateCode(inputPasscode)
        result && !message ? this.loadImages() : alert(message)
      } else {
        this.$router.push('/login')
      }

    } else {
      this.loadImages()
    }
  },
  methods: {

    /**
     * 加载图片资源
    */
    loadImages () {
      const shareData = require("@/data/shareData.json")
      this.slides = shareData
      this.displayNumbers = this.slides.length

      if (utils.isMobile()) this.mobileSettings()
    },

    /**
     * 移动端需要加载的配置
    */
    mobileSettings () {
      this.size.height = window.screen.availHeight
      this.size.width = window.screen.availWidth

      const base = this.size.height / this.size.width
      let baseSize = {
        width: 320,
        height: 320
      }

      if (base < 2) {
        baseSize.width = 450
        baseSize.height = 450
      }

      this.$set(this.slideSize, "width", baseSize.width)
      this.$set(this.slideSize, "height", baseSize.height)

      this.controlsVisible = false
    },

    /**
     * 控制音乐播放
    */
    clickPigImg () {
      this.$refs.MusicPlay.volume = .25
      if (this.$refs.MusicPlay.paused) {
        this.$refs.MusicPlay.play()
      } else {
        this.$refs.MusicPlay.pause()
      }
    },


    /**
     * 双击主 slide 图片播放音乐
    */
    doubleClickSlide (obj) {
      if (!obj.isCurrent) return false
      this.slideClickable = true
      this.clickCount += 1
      let timeoutNumber

      if (this.clickCount > 1) {
        this.clickCount = 0
        this.clickPigImg()
        clearTimeout(timeoutNumber)
        return true
      }

      timeoutNumber = setTimeout(() => {
        this.clickCount = 0
        clearTimeout(timeoutNumber)
      }, 500)
    },


    /**
     * 切换图片时增加防抖效果
    */
    switchingSlide () {
      this.slideClickable = false
      setTimeout(() => {
        this.slideClickable = true
      }, 500)
    }

  }
}
</script>

<style>
html {
  background-image: linear-gradient(45deg, #e3e0e0a1, transparent);
}

.images_body {
  padding: 2rem;
}

.carousel-3d-slide {
  box-shadow: -1px 15px 20px 0px;
  border-color: #00000005;
}

.carousel-3d-controls a span {
  color: #3333334a;
}

.show_img {
  height: 100%;
  display: block;
}

.music_body {
  position: absolute;
  right: 28px;
  top: 0;
  width: 40px;
  overflow: hidden;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  cursor: pointer;
}

.carousel-3d-slide {
  border-radius: 10px;
}

.media_img {
  width: 100%;
  height: 100%;
  display: block;
}
</style>




